import React from 'react'
import {CardHeader, Avatar, Typography} from "@material-ui/core";

const ShowProfile = (obj) =>
Object.keys(obj).map((k, index)=>{
       return (
          <CardHeader
              avatar={<Avatar> {k}</Avatar>}
              title={
                  <Typography  key={k} color="textPrimary" variant="subtitle1" wrap>
                      {k.toUpperCase()}
                  </Typography>
                  }
              subheader={
                  <Typography   color="textPrimary" variant="subtitle1" wrap>
                      {obj[k]}
                  </Typography>
              }
              subheader={
                  typeof obj[k] === "object"?ShowProfile(obj[k]):obj[k]
              }
          />
       )

})
export default ShowProfile