import axios from "axios";
import http from "../http-common";

const API_URL = "/users/";

const register = (data) => {
		let params={}
		let dataField=data.map((theField, k)=>{
			params[theField.name]=theField.value 
		})
  return http.post(API_URL, 
	  params
  );
};

const login = (data) => {
		let params={}
		let dataField=data.map((theField, k)=>{
			params[theField.name]=theField.value 
		})
		
  return http
    .post(API_URL + "login?include=User", 
      params
    )
    .then((response) => {
		console.log(response.data)
		
      if (response.data.id) {
        localStorage.setItem("user", JSON.stringify(response.data));
      }
      return localStorage.getItem("user");
    });
};

const logout = () => {
  localStorage.removeItem("user");
};

const getCurrentUser = () => {
  return JSON.parse(localStorage.getItem("user"));
};

export default {
  register,
  login,
  logout,
  getCurrentUser,
};
