import React, { useState } from 'react';
import { useHistory } from "react-router-dom";
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { useTranslation } from "react-i18next";
import 'translations/i18n.js';
import { useInput, useSubmit } from "hooks/form";
import AuthService from "services/auth.service"
import Profile from "pages/Profile"

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(2),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));
 
 const validations = {
  // eslint-disable-next-line
  EMAIL: /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
  PASSWORD:/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,20}$/
};


const Login=(props)=>{
  const classes = useStyles();
  const [message, setMessage] = useState("");
  const { t } = useTranslation();
  const history=useHistory()


  function handleValidation(value, regex) {
    // we could get fancy here with validations based on type of input
    // could be put in a form hook library and imported
    if (value && regex && value.match(regex)) return true;
    return false;
  }

  const email = useInput("email", "", handleValidation, validations.EMAIL);
  const password = useInput("password", "", handleValidation, validations.PASSWORD);
  const [data, setData] = useState(null);
    function handleSuccess(data) {
    // we're just setting the state here, but typically this would
    // be sent to the server for further validation and persistence
    setData(data);
console.log(data, 'data')
    AuthService.login(data).then(
      (response) => (
        history.push("/dashboard")

      ),
      (error) => {
        const resMessage =
           (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          setMessage(resMessage);
      }
    );
  }
  
 const submit = useSubmit([email, password], handleSuccess);

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
			{t('Sign in')}
        </Typography>
        <form className={classes.form} {...submit.props}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
            <TextField label="Email" fullWidth variant="outlined" {...email.props} />
            {email.props.error && (
              <Typography variant="body1" color="error">
                Invalid Email address
              </Typography>
            )}
            </Grid>
            <Grid item xs={12}>
            <TextField type="password" label={t("Password")} fullWidth variant="outlined" {...password.props} />
            {password.props.error && (
              <Typography variant="body1" color="error">
                Password needs 6 to 20 characters contain at least one numeric digit, one uppercase, and one lowercase
              </Typography>
            )}
          </Grid>
		  </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
		  {t('SIGN IN')}
          </Button>
		    {submit.errorItems && submit.errorItems.length > 0 && (
            <Typography variant="body1" color="error">
              {`Please fix ${submit.errorItems && submit.errorItems.length} form
            field error(s)`}
            </Typography>
          )}
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2">
			  {t('Forgot password?')}
              </Link>
            </Grid>
            <Grid item>
              <Link href="#" variant="body2">
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>

    </Container>
  );
}
export default Login